.button {
  width: 4em;
  height: 4em;

  padding: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  border: none;
  background: transparent;
  -webkit-tap-highlight-color: transparent;
}

.button__img {
  width: 4em;
  height: 4em;
}

.button--filter {
  width: 150px;
}

.button--like {
  width: 100%;
}

.button--delete {
  position: absolute;
  top: 0;
  right: 0;
}

.button--delete .button__img {
  width: 1.8em;
  height: 1.8em;

  border-radius: 50%;

  transform: translate(5px, -5px);
}

/* hover */
@media (hover) {
  .button:hover {
    cursor: pointer;
  }

  .button:focus {
    outline: none;
  }

  .button:focus path {
    stroke: #ff0000;
    stroke-width: 2.5;

    transition: stroke-width 0.3s, fill 0.3s;
  }
}

@media screen and (max-width: 425px) {
  .button--delete {
    position: absolute;
    top: -5px;
    right: -5px;
  }
}
